@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@600&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: "Quicksand";
  color: #333;
}

html{
  scroll-behavior: smooth;
}

.home{
  background-color:#010c20;
  max-width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.home-content{
  width: 95%;
  min-height: 100vh;
  background-color: #010c20;
  border-radius: 1.7rem;
}

.navbar{
  padding: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 80%;
  margin: 0 auto;
}

.navbar h1{
  color: #F0FFFE;
  margin-right: auto;
  font-size: 2.5rem;
  font-weight: bolder;
}

.navbar.links{
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  color: #F0FFFE;
}

.links a{
  margin-left: 3rem;
  text-decoration: none;
  padding: 6px;
  color: #F0FFFE;
  font-size: 1.5rem;
  font-weight: bolder;
  transition: 0.5s ease-in;
}

.navbar a:hover{
  color: #02faee;
}

.navbar button{
  display: block;
  min-height: 7.5vh;
  width: 100%;
  border: none;
  border-radius: 15px;
  margin-left: 3rem;
  padding: 6px;
  font-size: 1.5rem;
  font-weight: bolder;
  background-color: #02faee;
  color: #14274E;
  cursor: pointer;
  transition: 0.5s ease-in;
}

.nav-button{
  display: block;
  text-decoration: none;
  color: inherit;
}

.navbar button:hover{
  background: #010c20;
  color: #02faee;
  border: 1px solid#02faee;
}


.banner-content{
  padding: 1.5rem;
  max-width: 100%;
  border-left: 1px solid#02faee;
  margin-left: 8.5rem;
  margin-top: 5rem;
  line-height: 5rem;
}

.banner-content h1{
  font-size: 5rem;
  color: #ffffff;
}

.banner-content h2{
  font-size: 3rem;
  color: #ffffff;
}


.banner-content h3{
  font-size: 1.2rem;
  color: #ffffff;
}

.banner-link{
  min-height: 6.5vh;
  width: 13%;
  background: none;
  margin-top: 1rem;
  margin-right: 2rem;
  border: 1px solid #02faee;
  border-radius: 10px;
  color: #f1f1f1;
  font-size: 1.1rem;
  cursor: pointer;
  transition: 0.5s ease-in;
}

.banner-link a{
  display: block;
  color: inherit;
  text-decoration: none;
}

.banner-link a:hover{
  color: #14274E;
}

.banner-link:hover{
  color: #14274E;
  background: #02faee;
}

span{
  color: #02faee;
}

@media screen and (max-width: 950px){

  .navbar a{
    display: none;
  }

  .contact-link{
    display: none;
  }


  .home{
    background-color:#010c20;
    max-width: 100%;
    min-height: 50vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .home-content{
    max-width: 95%;
    min-height: 90vh;
    background-color: #010c20;
    border-radius: 1.7rem;
  }
  
  .banner-content{
    padding-left: 1rem;
    max-width: 80%;
    border-left: 1px solid#02faee;
    margin-left: 3rem;
    margin-top: 4rem;
    line-height: 2.5rem;
  }

  .banner-content h1{
    font-size: 3.5rem;
    line-height: 3.5rem;
    color: #ffffff;
  }
  
  .banner-content h2{
    font-size: 1.3rem;
    color: #ffffff;
  }
  
  
  .banner-content h3{
    font-size: .89rem;
    color: #ffffff;
  }

  .banner-link{
    min-height: 7vh;
    width: 30%;
    background: none;
    margin-top: 1rem;
    margin-right: 1rem;
    border: 1px solid #02faee;
    border-radius: 10px;
    color: #f1f1f1;
    outline: none;
    font-size: .9rem;
  }
  
}


/*-------- About page --------*/


.about-section{
  background-color: rgb(2, 250, 238, 0.2);
  padding: 4rem 5rem;
  max-width: 1500px;
  display: -webkit-flex;
  display: flex;
}

.about-text{
  margin-left: 2rem;
  max-width: 1000px;
  padding-left: 5rem;
  padding-top: 3rem;
}

.about-text h2{
  color: #14274E;
  max-width: 1000px;
  text-align: center;
  font-size: 4rem;
  padding-bottom: 2rem;
}

.about-text p{
  color: #14274E;
  max-width: 600px;
  line-height: 2.9rem;
  margin-left: 2rem;
  font-size: 1.5rem;
}

.about-text button{
  margin-left: 12rem;
  margin-top: 2rem;
  color: #f1f1f1;
  padding: 30px 25px;
  font-size: 1.5rem;
  border-radius: 15px;
  outline: none;
  border: none;
  background: #010c20;
  cursor: pointer;
  transition: 0.5s ease-in;
}

.about-text button:hover{
  background: none;
  color: #14274E;
  border: 1px solid #14274E;
}

.about-image img{
  max-width: 500px;
  object-fit: contain;
  padding: 1rem;
}

@media screen and (max-width: 950px){
  .about-image{
    display: none;
  }

  .about-section{
    background-color: #F0FFFE;
    padding: 1rem 1rem;
    max-width: 1500px;
    display: -webkit-flex;
    display: flex;
  }

  .about-text{
    max-width: 1500px;
    padding: 2rem;
    
  }

  .about-text h2{
    color: #14274E;
    max-width: 1200px;
    font-size: 2rem;
    padding-bottom: 1rem;
    padding-right: 2rem;
  }

  .about-text p{
    color: #14274E;
    max-width: 600px;
    line-height: 2.5rem;
    margin-left: 0rem;
    font-size: 1.2rem;
  }

  .about-text button{
    margin-left: 6rem;
    margin-top: 1.5rem;
    color: #f1f1f1;
    padding: 25px 20px;
    font-size: 1rem;
    border-radius: 15px;
    border: none;
    background: #14274E;
  }
}






